.Footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  color: white;
  padding: 2rem 0 0 0;
  width: 100%;

  .Footer-title {
    text-transform: uppercase;
    color: rgb(150, 150, 150);
    font-weight: 500;
  }

  .Footer-section-1 {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    margin-bottom: 2rem;

    span {
      margin-bottom: 1rem;
    }
    .Input-container {
      width: 25%;
      min-width: 16rem;
      max-width: 22rem;
      position: relative;
    input {
      width: 100%;
      height: 1.7rem;
      padding-left: 1rem;
      border: none;
    }

    input:focus {
      outline: 2px solid rgb(255, 236, 160);
      
    }
    .Footer-input-icon {
      position: absolute;
      right: .2rem;
      top: .2rem;   
      color: rgb(74, 74, 74);
      font-size: 1.4rem;
      cursor: pointer;
 }
  }
  } 

  .Footer-section-2, .Footer-section-3,.Footer-section-4{
    width: 100%;
    text-align: center;
    margin: 1.3rem 0;
    max-width: 70rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .Footer-link-parent{
      display: flex;
      list-style: none;
      justify-content: space-around;
      align-items: center;   
      width: 70%;
      font-size: .9rem;
      text-transform: capitalize;
      font-weight: 600;
      margin: 1.3rem;
      flex-wrap: wrap;
      margin-left: -1rem;

      a {
        color: white;
        text-decoration: none;
        cursor: pointer;
        margin: 0 .1rem;
      }

      a:hover {
        text-decoration: underline;
      }

      li {
        margin: .7rem 0;
        width: 12rem;
  
      }
    }    
  }
  
  .Footer-section-4 {
    max-width: 90rem;
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;

    .sec-4-cont {
      width: 15rem;
    }

    .Footer-logo-cont {
      font-size: 2rem;
      color: rgb(130, 130, 130);
      display: flex;
      justify-content: space-around;
      margin: .8rem 0 2rem 0;
      .fa-dhl {
        font-size: 4rem;
        margin-top: -1rem;
      }
    }
    .Footer-logo-cont-contact {
      color: rgb(238, 238, 238);
    }
  }
}

