
.Top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0 1rem 0;

  .Logo-wrapper {
    display: flex;
    width: 5rem;
    
    .Logo-icon {
      width: 3rem;
      object-position: center;
    }
    
    .Logo-name {
      text-align: center;
      font-size: 1.35rem;
      text-transform: uppercase;
      font-weight: 600;
      color: rgb(75, 75, 75); 
      padding-left: 0.5rem;
    }

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      color: black;
    }
  }

  .Top-right-wrapper {
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 35rem;
    margin-left: 2rem;

 
  .Search-cart-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .Search-bar {
      width: 55%;
      display: flex;
      margin-right: 4rem;

      input {
      border: none;
      background-color: rgb(240, 240, 240);
      width: 100%;
      height: 2.3rem;
      padding-left: 1rem;
      }

      input:focus {
        outline: none;
      }
      
      button {
        height: 2.3rem;
        width: 5rem;
        border: none;
        background-color: rgb(240, 240, 240);
        border-left: .1rem solid rgb(222, 222, 222);
        cursor: pointer;

        img {
          width: 1rem;
          margin-top: .1rem;
        }
      }
      @media only screen and (max-width: 700px) {
        display: none;
      }
    }
    .Shopping-icon-container {
      text-decoration: none;
      position: relative;

    .Shopping-icon {
      width: 1.6rem;
      font-size: 2rem;
      width: 1.9rem;
      color: black;
    }

    .Shopping-counter{
      width: 1rem;
      text-align: center;
      font-size: .8rem;
      font-weight: 600;
      border-radius: 50%;
      position: absolute;
      top:1.2rem ;
      left: 1.1rem;
      background-color: red;
      border-radius: 50%;
    }
    .Shopping-counter-badge{
      color: rgb(255, 255, 255);
    }
  }
  }
}
}