.reviews-section {
  background-image: url("./gray-background.jpg");
  background-position: center;
  background-size: cover;
  padding: 2rem 2.5rem; 
  margin-bottom: 1.3rem;
  border: 1px solid rgba(175, 175, 175, 0.199);
  @media screen and (max-width: 800px) {
    padding: 2rem 1rem; 
  }


  h3 {
    font-size: 0.9rem;
    padding: 0.5rem 0; 
    text-align: center;
    margin: 0 auto; 
    font-weight: 600;
    background: white;
    border: 1px solid rgba(175, 175, 175, 0.658);
    color: black;
    max-width: 13rem;
    text-transform: uppercase;
  }

  p {
    text-align: center;
    margin: 1rem 0 1.5rem 0;
  };
  .reviews-section__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .reviews-section-button-box {
    text-align: center;
    button {
      max-width: 17rem;
      padding: 0.625rem 1rem;
      margin: 1rem auto 0 auto;
      transition: 0.3s;
      border: 1px solid rgb(211, 211, 211);
      background-color: rgb(31, 31, 31);
      color: rgb(229, 229, 229);
      font-weight: 500;
      text-transform: uppercase;

      &:hover {
        background-color: #000000;
        color: white;
        cursor: pointer;
      }
    }
  }
}

